	function setSchoolDistricts(countyValue){
		var matchedSchools = []
		var listElements = []
		var $list = $('#enroll_school_district_list')

		// Filter schools by the selected county
		for (var i = 0; i < enrollNominationSchoolList.length; i++) {
			const county = enrollNominationSchoolList[i].schoolCounty.toLowerCase().split('.').join('')
			const selected = countyValue.toLowerCase().split('.').join('')
			if (county === selected) {
				matchedSchools.push(enrollNominationSchoolList[i]);
			}
		}
		matchedSchools.sort(function(a, b){
			return a.schoolName < b.schoolName ? -1 : 1
		})

		// render matched schools as <li> elements
		for(var v = 0; v < matchedSchools.length; v++){
			var school = matchedSchools[v]
			listElements.push('<li role="option" class="enroll_school_option" data-county-id="' + school.countyID +'" data-value=' + school.ncesID + ' data-name="' + school.schoolName +'">' + school.schoolName + '</li>')
		}
		$list.html('').append(listElements.join(''))

		// side-effect...
		bindDistrictClick();
	}
	function bindDistrictClick(){
		$('.enroll_school_option').click(function(e) {
			e.stopPropagation();
			var schoolValue = $(this).attr('data-value');
			var niceName = $(this).attr('data-name');
			var countyID = $(this).attr('data-county-id');
			//Pop that data into the dropdown so you see your selection
			$('.enroll_school_district_select_top').removeClass('hasError');
			$('#enroll_school_district').removeClass('parsley-error');
			$('#enroll_school_district_nicename').html(niceName);
			$('.enroll_school_district_select_bottom').removeClass('active');
			if($('#enroll_school_district_chevron').hasClass('fa-chevron-up')){
				$('#enroll_school_district_chevron').removeClass('fa-chevron-up').addClass('fa-chevron-down');
			}
			$('#enroll_school_district').val(schoolValue);
			$('#enroll_school_district_name').val(niceName);
			$('#enroll_school_county_id').val(countyID);
		})
	}
	//Handle submission of code based enrollment
	$('#GC_Enrollment_lookup').on('submit', function(e){
		e.preventDefault();
		var code = $('#gc_code').val();
		submitGCCode(code);
	})

	//Handle submission of code based enrollment
	$('#GC_Rural_Enrollment_lookup').on('submit', function(e){
		e.preventDefault();
		var code = $('#gc_code').val();
		submitRuralCode(code);
	})

	//Handles submission by code, then pre-populating the form below
	function submitGCCode(code){
		var targetURL = wpAjax.ajaxurl + '?action=get_community_enrollment_by_code&code='+code;
		$('#Code_Spinner').addClass('active');
		$('.code_submission_fail').removeClass('active');
		$.ajax(targetURL, {
			'method': 'GET'
		})
		.done(function(data){
			var parsedData = JSON.parse(data.data);
			console.log(parsedData);

			$('#Code_Spinner').removeClass('active');
			if(data.data == null || data.data== 'null' || parsedData.RequestStatus == 'Record Not Found'){
				$('.code_submission_fail').addClass('active');
				$('#Code_Spinner').removeClass('active');
				$('.info-span-after-code').addClass('hide');
			}
			else {
				handleCodeSubmissionResponse(parsedData);
				$('#Code_Spinner').removeClass('active');
			}
		})
		.fail(function() {
			console.log('it failed');
			$('.info-span-after-code').addClass('hide');
			$('.code_submission_fail').addClass('active');
			$('#Code_Spinner').removeClass('active');
		})
	}

	function submitRuralCode(code){
		var targetURL = wpAjax.ajaxurl + '?action=get_rural_enrollment_by_code&code='+code;
		$('#Code_Spinner').addClass('active');
		$('.code_submission_fail').removeClass('active');
		$.ajax(targetURL, {
			'method': 'GET'
		})
		.done(function(data){
			var parsedData = JSON.parse(data.data);
			console.log(parsedData);

			$('#Code_Spinner').removeClass('active');
			if(data.data == null || data.data== 'null' || parsedData.RequestStatus == 'Record Not Found'){
				$('.code_submission_fail').addClass('active');
				$('#Code_Spinner').removeClass('active');
				$('.info-span-after-code').addClass('hide');
			}
			else {
				handleCodeSubmissionResponse(parsedData);
				$('#Code_Spinner').removeClass('active');
			}
		})
		.fail(function() {
			console.log('it failed');
			$('.info-span-after-code').addClass('hide');
			$('.code_submission_fail').addClass('active');
			$('#Code_Spinner').removeClass('active');
		})
	}

	//dumb helper function to capitalize first letter of a string because, well, data be bad
	function capitalizeFirstLetter(string) {
    	return string.charAt(0).toUpperCase() + string.slice(1);
	}
	//handle pre-filling form with crap from code submission
	function handleCodeSubmissionResponse(parsedData){
		$('#first_name').val(parsedData.First_Name);
		$('#last_name').val(parsedData.Last_Name);
		$('#email').val(parsedData.Email);
		$('#address').val(parsedData.Street_1);
		$('#apartment').val(parsedData.Street_2);
		$('#city').val(parsedData.city);
		//Here we just trigger a click on the right state option so that all the surrounding junk gets fired and the county dropdown gets populated right
		$('#state_select_list_wrapper').find("[data-value='" + parsedData.State + "']").trigger('click');
		$('#zip').val(parsedData.Zip_Postal_Code);
		//Same thing for clicking the county
		if(parsedData.County_Name){
			$('#enroll_county_list').find("[data-value='" + capitalizeFirstLetter(parsedData.County_Name.toLowerCase()) + "']").trigger('click');
		}
		$('#phone').val(parsedData.Phone);

		//Add touched to all the form fields to move the labels up
		$('.form-field').addClass('touched');
		$('.code_submission_success').addClass('active');
		$('#GC_Enrollment_lookup').addClass('complete');
		$('.info-span-after-code').addClass('hide');
	}

	//handle checkboxes
	$('.enroll_checkbox').bind('click', toggleCheckbox);

	function toggleCheckbox() {

		$(this).parent('.checkbox-field').find('label').toggleClass('checked');
		if( $(this).parent('.checkbox-field').find('label').hasClass('checked')){
			$(this).parent('.checkbox-field').removeClass('error');
		}

	}

